import * as React from "react"
import { useState, useEffect } from "react"
import { HeadFC, PageProps, graphql, useStaticQuery } from "gatsby"
import { breakNumber, numberToStar, numberMeaning, stars, combo, alphaToNumber } from '../helpers/NumberEnergy'
import Ad from '../components/ad'
import { Trans, useTranslation } from 'gatsby-plugin-react-i18next';
import Layout from '../components/layout/layout'
import Parse from '../vendor/Parse'

const IndexPage: React.FC<PageProps> = () => {

  const { t } = useTranslation();
  const [numbers, setNumbers] = useState([]);
  const [loading, setLoading] = useState(false);
  const isGoodEnergy = (star) => ['tianyi', 'yannian', 'shengqi'].includes(star.trim())

  useEffect(() => {
    setLoading(true)
    Parse.Cloud.run("findGoodNumber").then(e => {
      setNumbers(e)
      setLoading(false)
    })

  }, [])

  return (
    <Layout>
      <main className="page-main">
        <div className="section-info">
          <div className="uk-section-large uk-container">
            <div className="section-title uk-text-center"><span>{t('contactSales')} </span>
              <h4 className="uk-h4"> {t('Group A')} </h4>
              <div className="star-chart">
                {
                  loading && "Loading......"
                }
                {
                  numbers.map((s: any, i: number) => <>
                    <div className={`div${i + 1}`}> {s} </div>
                  </>)
                }
              </div>
            </div>
          </div>
        </div>

      </main>
    </Layout>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Is Your Number Lucky?</title>


export const query = graphql`
  query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
